import { Component } from '@angular/core';
import { AlertController, NavController } from '@ionic/angular';
import { Geolocation } from '@ionic-native/geolocation/ngx';
import { AuxiliarService } from '../services/auxiliar.service';
import { Coords } from '../models/coords';
import * as firebase from 'firebase'

@Component({
  selector: 'app-tab1',
  templateUrl: 'tab1.page.html',
  styleUrls: ['tab1.page.scss']
})
export class Tab1Page {
  origin = new Coords()
  destination = new Coords()

  constructor(
    private geoController: Geolocation, 
    private alertController: AlertController, 
    private aux: AuxiliarService, 
    private navController: NavController) {
  }

  getLocation(){
    this.geoController.getCurrentPosition().then(position => {
      this.origin.lat = position.coords.latitude;
      this.origin.lng = position.coords.longitude;
    }).catch( err =>{
      this.onError(err)
    })
  }

  async onError(err){
    const alert = await this.alertController.create({
      header: "Error",
      subHeader: err
    })
    await alert.present()
  }

  send($event){
    this.destination.lat = $event.coords.lat;
    this.destination.lng = $event.coords.lng;
    let indications = {
      origin: this.origin,
      destination: this.destination
    }
    console.table(indications)
    this.aux.setParams("indicaciones",indications)
    this.navController.navigateForward("tabs/tab2")
  }

  ionViewDidEnter(){
    this.getLocation()
    let nombre= firebase.auth().currentUser;
    console.log(nombre.displayName);
  }


}
